<template>
  <div class="card">
    <div class="card-header">
      Read
    </div>
    <div class="card-body">
      <div v-if="board != null">
        <div class="d-flex">
          <div>
            <p>bno: {{board.bno}}</p>
            <p>btitle: {{board.btitle}}</p>
            <p>bcontent: {{board.bcontent}}</p>
            <p>mid: {{board.mid}}</p>
            <p>bdate: {{new Date(board.bdate).toLocaleDateString()}}</p>
            <p>bhitcount: {{board.bhitcount}}</p>
            <p v-if="board.battachoname != null">
              battachoname: {{board.battachoname}}
              <button @click="downloadBattach()" class="btn btn-info btn-sm ml-2">다운로드</button>
            </p>
          </div>
          <div class="ml-5" >
            <img v-if="board.battachoname != null" :src="battach" width="300"/>
          </div>
        </div>
        <div>
          <router-link :to="`/menu07/board/list?pageNo=${pageNo}`" 
            class="btn btn-info btn-sm mr-2">목록</router-link>
          <router-link :to="`/menu07/board/updateform?bno=${bno}&pageNo=${pageNo}`" 
            class="btn btn-info btn-sm mr-2">수정</router-link>
          <button class="btn btn-danger btn-sm mr-2" @click="handleRemove()">삭제</button>
        </div>
        
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useRoute, useRouter } from "vue-router";
import apiBoard from "@/apis/board";

//npm install js-file-download
import fileDownload from "js-file-download";
import axios from "axios";

const route = useRoute();
const router = useRouter();

const bno = route.query.bno;
const pageNo = route.query.pageNo;
const hit = route.query.hit;
const board = ref(null);
const battach = ref(null);

async function getBoard() {
  apiBoard.readBoard(bno, hit)  //내부에 비동기를 작성해도 괜찮다.(리턴 값이 있는 함수가 아니라 그냥 값이 오면 반응형 변수에 값을 대입하는게 전부이기 때문)
  .then((data) => {
    board.value = data;
  });
  const blob = await apiBoard.downloadBoardAttach(bno);
  if(blob != null) {
    battach.value = URL.createObjectURL(blob);  //메모리에 있는 것을 URL형태로 만들어서 넣어준다.
  }
  console.log(blob)
}

getBoard();

async function downloadBattach() {
  const blob = await apiBoard.downloadBoardAttach(bno);
  if(blob != null) {
    fileDownload(blob, board.value.battachoname);
  }
}

async function handleRemove() {
  const result = await apiBoard.deleteBoard(bno);
  if(result === "success") {
    router.push(`/menu07/board/list?pageNo=${pageNo}`)
  }
}
</script>

