import axios from "axios";
import apiAuth from "@/apis/auth";
//게시물 목록 요청
async function getBoardList(pageNo = 1) {
  //async는 함수를 비동기로 만들고, 내부는 비동기가 되면 안되기 때문에 await함수로 동기처리 한다.
  //(근데 비동기로 될 수도 있음?!) 근데 여기선 return 값이 있기 때문에 내부에 비동기를 사용하면 안될 듯(비동기 응답이 오기도 전에 리턴을 해버리면 안되니까)
  let response = null;
  try {
    response = await axios.get("/board/list", { params: { pageNo } });
    return { result: "success", data: response.data };
  } catch (error) {
    console.log(error);
    if (error.response) {
      //서버응답이 있는 경우
      if (error.response.status === 403 || error.response.status === 302) {
        console.log("~~~~~~~~~~~~~~~");
        if (await apiAuth.refreshToken()) {
          //재귀를 사용하지 않는 이유는 비동기 내부에 또 비동기가 있으면 중복이 되기 때문?
          response = await axios.get("/board/list", { params: { pageNo } });
          return { result: "success", data: response.data };
        }
      }
    } else {
      //서버응답이 없는 경우 == 네트워크 오류
      return { result: "fail-network" };
    }
  }
  console.log(response);
}

//게시물 쓰기(멀티파트)
async function createBoard(multipartFormData) {
  let dbBoard = null;
  try {
    const response = await axios.post("/board/", multipartFormData);
    dbBoard = response.data;
  } catch (error) {
    if (error.response) {
      //서버응답이 있는 경우
      if (error.response.status === 403) {
        console.log("~~~~~~~~~~~~~~~");
        if (await apiAuth.refreshToken()) {
          //재귀를 사용하지 않는 이유는 비동기 내부에 또 비동기가 있으면 중복이 되기 때문?
          const response = await axios.post("/board/", multipartFormData);
          dbBoard = response.data;
        }
      }
    }
  }
  return dbBoard;
}

//게시물 가져오기
async function readBoard(bno, hit) {
  let board = null;
  try {
    const response = await axios.get(`/board/${bno}?hit=${hit}`);
    board = response.data;
  } catch (error) {
    if (error.response) {
      //서버응답이 있는 경우
      if (error.response.status === 403) {
        console.log("~~~~~~~~~~~~~~~");
        if (await apiAuth.refreshToken()) {
          //재귀를 사용하지 않는 이유는 비동기 내부에 또 비동기가 있으면 중복이 되기 때문?
          const response = await axios.get(`/board/${bno}?hit=${hit}`);
          board = response.data;
        }
      }
    }
  }
  return board;
}

//파일 다운로드
async function downloadBoardAttach(bno) {
  let blob = null;
  try {
    const response = await axios.get(`/board/battach/${bno}`, { responseType: "blob" }); //다운로드 받는 것이 blob 타입의 바이너리 데이터라고 명시해야한다.
    blob = response.data;
  } catch (error) {
    if (error.response) {
      //서버응답이 있는 경우
      if (error.response.status === 403) {
        console.log("~~~~~~~~~~~~~~~");
        if (await apiAuth.refreshToken()) {
          //재귀를 사용하지 않는 이유는 비동기 내부에 또 비동기가 있으면 중복이 되기 때문?
          const response = await axios.get(`/board/battach/${bno}`, { responseType: "blob" }); //다운로드 받는 것이 blob 타입의 바이너리 데이터라고 명시해야한다.
          blob = response.data;
        }
      }
    }
  }
  return blob;
}

//게시물 삭제
async function deleteBoard(bno) {
  let result = null;
  try {
    const response = await axios.delete(`/board/${bno}`);
    result = response.data.result;
  } catch (error) {
    if (error.response) {
      //서버응답이 있는 경우
      if (error.response.status === 403) {
        console.log("~~~~~~~~~~~~~~~");
        if (await apiAuth.refreshToken()) {
          //재귀를 사용하지 않는 이유는 비동기 내부에 또 비동기가 있으면 중복이 되기 때문?
          const response = await axios.delete(`/board/${bno}`);
          result = response.data.result;
        }
      }
    }
  }
  return result;
}

//게시물 수정
async function updateBoard(multipartFormData) {
  let dbBoard = null;
  try {
    const response = await axios.put(`/board/`, multipartFormData);
    dbBoard = response.data;
  } catch (error) {
    if (error.response) {
      //서버응답이 있는 경우
      if (error.response.status === 403) {
        console.log("~~~~~~~~~~~~~~~");
        if (await apiAuth.refreshToken()) {
          //재귀를 사용하지 않는 이유는 비동기 내부에 또 비동기가 있으면 중복이 되기 때문?
          const response = await axios.put(`/board/`, multipartFormData);
          dbBoard = response.data;
        }
      }
    }
  }
  return dbBoard;
}

//내보내기
export default {
  getBoardList,
  createBoard,
  readBoard,
  downloadBoardAttach,
  deleteBoard,
  updateBoard,
};
